










































































































































import Axios from 'axios';
import { Component, Vue } from 'vue-property-decorator';
import { Player } from '../models/Player';
import { State } from '../models/State';

@Component
export default class GameComponent extends Vue {
  public currentState: State = {} as State;

  public actions: Array<object> = [];

  public deck: object = {
    1: { id: 1, suite: 'spade', type: '6' },
    2: { id: 2, suite: 'club', type: '6' },
    3: { id: 3, suite: 'diamond', type: '6' },
    4: { id: 4, suite: 'hart', type: '6' },
    5: { id: 5, suite: 'spade', type: '7' },
    6: { id: 6, suite: 'club', type: '7' },
    7: { id: 7, suite: 'diamond', type: '7' },
    8: { id: 8, suite: 'hart', type: '7' },
    9: { id: 9, suite: 'spade', type: '8' },
    10: { id: 10, suite: 'club', type: '8' },
    11: { id: 11, suite: 'diamond', type: '8' },
    12: { id: 12, suite: 'hart', type: '8' },
    13: { id: 13, suite: 'spade', type: '9' },
    14: { id: 14, suite: 'club', type: '9' },
    15: { id: 15, suite: 'diamond', type: '9' },
    16: { id: 16, suite: 'hart', type: '9' },
    17: { id: 17, suite: 'spade', type: '10' },
    18: { id: 18, suite: 'club', type: '10' },
    19: { id: 19, suite: 'diamond', type: '10' },
    20: { id: 20, suite: 'hart', type: '10' },
    21: { id: 21, suite: 'spade', type: 'J' },
    22: { id: 22, suite: 'club', type: 'J' },
    23: { id: 23, suite: 'diamond', type: 'J' },
    24: { id: 24, suite: 'hart', type: 'J' },
    25: { id: 25, suite: 'spade', type: 'Q' },
    26: { id: 26, suite: 'club', type: 'Q' },
    27: { id: 27, suite: 'diamond', type: 'Q' },
    28: { id: 28, suite: 'hart', type: 'Q' },
    29: { id: 29, suite: 'spade', type: 'K' },
    30: { id: 30, suite: 'club', type: 'K' },
    31: { id: 31, suite: 'diamond', type: 'K' },
    32: { id: 32, suite: 'hart', type: 'K' },
    33: { id: 33, suite: 'spade', type: 'A' },
    34: { id: 34, suite: 'club', type: 'A' },
    35: { id: 35, suite: 'diamond', type: 'A' },
    36: { id: 36, suite: 'hart', type: 'A' },
  };

  public me: Player = {};

  public players: Array<Player> = [];

  public opponents: Array<Player> = [];

  public kozyr = 'none';

  public polling = -1;

  public gameId = '';

  created() {
    this.refreshState();
    this.gameId = this.$route.params.gameId;
    this.polling = setInterval(() => {
      this.refreshState();
    }, 1000);
  }

  action(card: any) {
    if (this.currentState.currentTurnPosition && this.currentState.actingPlayerPosition) {
      const currentPlayer: Player = this.me;
      if (currentPlayer && currentPlayer.cards
          && currentPlayer.cards[0] && currentPlayer.cards[0]) {
        if (this.currentState.currentTurnPosition !== this.currentState.actingPlayerPosition) {
          Axios.get(`http://localhost:8080/reaction?playerId=${currentPlayer.id}&cardId=${card.id}&gameId=${this.gameId}`).then((res) => {
            this.currentState = res.data.state;
            if (this.currentState && this.currentState.currentPlay
                && this.currentState.currentPlay.actions) {
              this.actions = this.currentState.currentPlay.actions;
            }
          }).catch((error: any) => {
            if (error.response) {
              alert(error.response.data.message);
            }
          });
        } else {
          Axios.get(`http://localhost:8080/action?playerId=${currentPlayer.id}&cardId=${card.id}&gameId=${this.gameId}`).then((res) => {
            this.currentState = res.data.state;
            if (this.currentState && this.currentState.currentPlay
                && this.currentState.currentPlay.actions) {
              this.actions = this.currentState.currentPlay.actions;
            }
          }).catch((error: any) => {
            if (error.response) {
              alert(error.response.data.message);
            }
          });
        }
      }
    }
  }

  call(quantity: number) {
    if (this.currentState.currentTurnPosition) {
      const currentPlayer: Player = this.players[this.currentState.currentTurnPosition];
      Axios.get(`http://localhost:8080/call?playerId=${currentPlayer.id}&wantQty=${quantity}&gameId=${this.gameId}`).then((res) => {
        this.currentState = res.data.state;
      }).catch((error: any) => {
        if (error.response) {
          alert(error.response.data.message);
        }
      });
    }
  }

  refreshState() {
    const { gameId, id } = this.$route.params;
    Axios.get(`http://localhost:8080/getPlayersState?playerId=${id}&gameId=${gameId}`).then((res) => {
      this.currentState = res.data.state;
      if (this.currentState && this.currentState.currentPlay
          && this.currentState.currentPlay.actions) {
        this.actions = this.currentState.currentPlay.actions;
      }
      this.me = res.data.player;
      this.players[res.data.player.position] = res.data.player;
      this.opponents = res.data.opponents;
      res.data.opponents.forEach((player: Player) => {
        if (player.position) {
          this.players[player.position] = player;
        }
      });
      this.kozyr = res.data.state.currentPlay.kozyr;
    }).catch((error: any) => {
      if (error.response) {
        alert(error.response.data.message);
      }
    });
  }

  beforeDestroy() {
    clearInterval(this.polling);
    this.polling = -1;
  }

  test() {
    Axios.get('http://localhost:8080/testPrevHand').then((res) => {
      this.currentState = res.data.state;
    }).catch((error: any) => {
      if (error.response) {
        alert(error.response.data.message);
      }
    });
  }
}
